<script>
    import '@/common/js/dateFormat.js';
    let userInfo=JSON.stringify(sessionStorage.getItem('cluser'));
    export default {
        data() {
            return {
                selectTab:1,
                displayInOut:1,
                startDate: new Date(new Date().getTime() - 24 * 60 * 60 * 1000).Format('yyyy-MM-dd'),//
                startTime: '00:00',//
                endDate: new Date().Format('yyyy-MM-dd'),//
                endTime: '00:00',//
                conditionShow: true,//
                brand: [],//
                brandList: [],//
                spec: [],//
                specList: [],//
                packing:[],//
                packList:[{name:''},{name:''}],//
                area:[],//
                areaList:[],//
                pro:[],//
                proList:[],//,
                city:[],//
                cityList:[],//
                county:[],//
                countyList:[],//
                dialogTableVisible: false,//
                dialogTableVisible1: false,//
                loadValue:-1,//
                historyValue:-1,//
                loadList:[],//
                historyList:[],//
                tabActive:0,//tab
                tabList:['','','','',''],//tab
                loading:true,//table
                tableData:[],//
                tableData2:[],//
                tableData3:[],//
                tableData4:[],//
                tableData5:[],//
                startDateTime: new Date().getTime() - 24 * 60 * 60 * 1000,
                endDateTime: new Date().getTime(),
            };
        },
        mounted() {
            this.getProvInout();
            this.getBrand();
            this.getArea();
            var that=this;
            var inter=setInterval(()=>{
                if(that.brand.length!=0){
                    this.itemSearch(0);
                    clearInterval(inter);
                    inter=null;
                }
            },10)

        },
        computed:{
            transformStart(){
                // var str=this.startDate+'_'+this.startTime.substr(0,2);
                var str=this.startDate;
                return str;
            },
            transformEnd(){
                // var str=this.endDate+'_'+this.endTime.substr(0,2);
                var str=this.endDate;
                return str;
            },
            transformBrand(){
                var str='';
                if(this.brand.length!=0){
                    str=this.brand.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformSpec(){
                var str='';
                if(this.spec.length!=0){
                    str=this.spec.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformPack(){
                var str='';
                if(this.packing.length!=0){
                    str=this.packing.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformAreaShow(){
                if(this.area.length!=0){
                    var c=this.area;
                    let obj = [];
                    c.forEach((val,i)=>{
                        obj.push(this.areaList.find((item)=>{//userList
                            return item.code == val;//
                        }).name) ;
                    })
                    return obj.join(',')
                }else {
                    return '';
                }


                console.log(obj);//namelabel
            },
            transformArea(){
                var str='';
                if(this.area.length!=0){
                    str=this.area.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformPro(){
                var str='';
                if(this.pro.length!=0){
                    str=this.pro.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformCity(){
                var str='';
                if(this.city.length!=0){
                    str=this.city.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformCounty(){
                var str='';
                if(this.county.length!=0){
                    str=this.county.join(',')
                }else {
                    str=''
                }
                return str;
            },
            transformAreaCommon(){
                var that=this;
                return function(str){
                    if(str.length!=0){
                        let tempArr=str.split(',');
                        let obj = [];
                        tempArr.forEach((val)=>{
                            obj.push(that.areaList.find((item)=>{//userList
                                return item.code == val;//
                            }).name) ;
                        })
                        return obj.join(',')
                    }else {
                        return '';
                    }
                }
            }

        },
        methods: {
            //
            getProvInout(){
                var that = this;
                this.$request.post(
                    '/record/public/getDefaultProvInout', {},
                    true,
                    res => {
                        var data = res || [];
                        if(data.length != 0) {
                            this.displayInOut=data[0].code;
                        }
                    },
                    err => {
                        console.log(err)
                    }
                )
            },
            selectStartDay(day) {//
                this.startDateTime =  day.getTime()
                if(this.endDateTime<this.startDateTime){
                    alert("")
                    this.startDate = new Date(new Date().getTime() - 24 * 60 * 60 * 1000).Format('yyyy-MM-dd')
                    this.startDateTime =  new Date().getTime() - 24 * 60 * 60 * 1000
                }else{
                    this.startDate = day.Format('yyyy-MM-dd');
                }
                this.selectTime()
            },
            selectTime(){
                if(this.startDate == this.endDate){
                    let st = this.startTime.split(":")[0] - ""
                    let et = this.endTime.split(":")[0] - ""
                    if(st>et){
                        alert("")
                        this.startTime = "00:00"
                        this.endTime = "00:00"
                    }
                }
            },

            selectEndDay(day) {
                this.endDateTime = day.getTime()
                if(this.endDateTime<this.startDateTime){
                    alert("")
                    this.endDate = new Date().Format('yyyy-MM-dd')
                    this.endDateTime =  new Date().getTime()
                }else {
                    this.endDate = day.Format('yyyy-MM-dd');
                }
                this.selectTime()
            },
            toggleShow() {//
                if(this.conditionShow) {
                    this.conditionShow = false;
                } else {
                    this.conditionShow = true;
                }
            },
            getBrand() {//
                var that = this;
                this.$request.post(
                    '/record/public/getUserBrandByUserId', {

                    },
                    true,
                    res => {
                        var data = res || [];
                        that.brandList = data;
                        if(data.length != 0) {
                            that.brand.push(data[0].name);
                        }

                    },
                    err => {
                        console.log(err)
                    }
                )
            },
            getSpec(flag) {//
                var that = this;
                var params = that.brand.join(',');
                this.$request.post(
                    '/record/statistics/getProductOfNoNuit', {
                        productBrand:params
                    },
                    true,
                    res => {
                        var data = res || [];
                        that.specList = data;

                    },
                    err => {
                        console.log(err)
                    }
                )

            },
            getArea(){//
                var that = this;
                this.$request.post(
                    '/record/public/getUserSaleByUserId', {
                    },
                    true,
                    res => {
                        var data = res || [];
//						data=[{"code":"BJ","name":""},{"code":"DB","name":""},{"code":"DN","name":""},{"code":"GXYN","name":""},{"code":"HD","name":""},{"code":"HN","name":""},{"code":"HNHB","name":""},{"code":"NM","name":""},{"code":"O","name":""},{"code":"Q","name":""},{"code":"SD","name":""},{"code":"SN","name":""},{"code":"SX","name":""},{"code":"TJ","name":""},{"code":"XB","name":""},{"code":"XN","name":""}];
                        that.areaList = data;

                    },
                    err => {
                        console.log(err)
                    }
                )
            },
            getPro(){//
                var that = this;
                if(that.area.length!=0){
                    var params=that.area.join(',')
                }else {
                    var params='';
                }
