                    err => {
                        console.log(err)
                    }
                )
            },
            handleClose(tag){//
                if(tag==0){
                    this.brand=[];
                }else if(tag==1){
                    this.spec=[];
                }else if(tag==2){
                    this.packing=[];
                }else if(tag==3){
                    this.area=[];
                }else if(tag==4){
                    this.pro=[];
                }else if(tag==5){
                    this.city=[];
                }else if(tag==6){
                    this.county=[];
                }
            },
            exportExcel(){//
                var url = "getMultiScanDataExcel";
                if(this.selectTab==1){//
                    url = "getMultiScanDataExcel";
                }else if(this.selectTab==2){//
                    url = "getMultiKPIDataExcel";
                }else if(this.selectTab==3){//
                    url = "getMultiCashDataExcel";
                }else if(this.selectTab==4){//
                    url = "getMultiGoodDataExcel";
                }else if(this.selectTab==5){//
                    url = "getMultiScoreDataExcel";
                }
                var that = this;
                var specName='';
                var proName='';
                var ctName='';
                var cyName='';
                if(that.transformSpec.length!=0){
                    specName=that.transformSpec;
                }
                if(that.transformPro.length!=0){
                    proName=that.transformPro;
                }
                if(that.transformCity.length!=0){
                    ctName=that.transformCity;
                }
                if(that.transformCounty.length!=0){
                    cyName=that.transformCounty;
                }
                var params={
                    cityName:ctName,
                    startTime:that.transformStart,
                    endTime:that.transformEnd,
                    productBrand:that.transformBrand,
                    productName:specName,
                    productPack:that.transformPack,
                    provinceName:proName,
                    saleZone:that.transformArea,
                    countyName:cyName
                }
                var url = "/record/fixatreport/"+url;
                var xhr = new XMLHttpRequest();
                var formData = new FormData();
                for(var attr in params) {
                    formData.append(attr, params[attr]);
                }
                xhr.overrideMimeType("text/plain; charset=x-user-defined");
                xhr.open('POST', url, true);
                xhr.responseType = "blob";
                xhr.responseType = "arraybuffer"
                xhr.setRequestHeader("token", sessionStorage.getItem('access_token'));
                xhr.setRequestHeader("loginId", sessionStorage.getItem('access_loginId'));
                xhr.onload = function(res) {
                    if (this.status == 200) {
                        var blob = new Blob([this.response], {type: 'application/vnd.ms-excel'});
                        var respHeader = xhr.getResponseHeader("Content-Disposition");
                        var fileName = decodeURI(respHeader.match(/filename=(.*?)(;|$)/)[1]);
                        if (window.navigator.msSaveOrOpenBlob) {
                            navigator.msSaveBlob(blob, fileName);
                        } else {
                            var link = document.createElement('a');
                            link.href = window.URL.createObjectURL(blob);
                            link.download = fileName;
                            link.click();
                            window.URL.revokeObjectURL(link.href);
                        }
                    }
                }
                xhr.send(formData);
            }

        }
    }
</script>

<style lang="scss" scoped>
	.multi-root {
		color: #515151;
		.search-condition {
			background: #fff;
			border-radius: 4px;
			padding: 0 20px 20px 20px;
			.title {
				text-align: center;
				height: 60px;
				line-height: 60px;
				border-bottom: 1px solid #ccc;
				cursor: pointer;
				span {
					transition: All 0.4s ease-in-out;
					-webkit-transition: All 0.4s ease-in-out;
					-moz-transition: All 0.4s ease-in-out;
					-o-transition: All 0.4s ease-in-out;
					&.active {
						transform: rotateZ(180deg);
						-webkit-transform: rotateZ(180deg);
					}
				}
			}
			.condition-con {
				padding: 20px 10px 10px 10px;
				.search-result {
					margin: 20px 0 0 0;
					padding-top: 20px;
					border-top:1px solid #ccc;
					span {
						margin-bottom: 10px;
					}
				}
				.search-oper {
					white-space: nowrap;
					.oper-btn {
						width:20%;
						display: inline-block;
						vertical-align: middle;
						height: 60px;
						button {
							margin:20px auto;
							margin-left: 50%;
							transform: translateX(-50%);
						}
						.tpl-con {
							height: 300px;
							overflow-y: auto;
							.el-radio {
								display: block;
								margin-bottom: 20px;
							}
							.el-radio+.el-radio{
								margin-left: 0;
							}
						}
					}
				}
			}
		}
		.result-part {
			background: #fff;
			border-radius: 4px;
			padding: 20px 20px 20px 20px;
			margin-top: 20px;
			.result-tab {
				ul {
					height: 32px;
					li {
						float: left;
						font-size: 16px;
						line-height: 32px;
						padding: 0 10px;
						position: relative;
						cursor:pointer;
						&:before {
							content: '';
							width:85%;
							height: 1px;
							position: absolute;
							bottom:0;
							left: 50%;
							transform: translateX(-50%);
							background: #4A7DE1;
							display: none;
						}
						&:hover {
							color:#4A7DE1;
							&:before {
								display: inline-block;
							}
						}
						&.active {
							color:#0077FF;
							&:before {
								background: #0077FF;
								display: inline-block;
							}
						}
					}
				}
			}
		}
	}
</style>



